/* Icons */
.login__main_home--svg {
    width: 30px;
    margin-left: 20px;
    margin-top: 12px;
}

.login__main_home--icons {
    width: 26px;
    margin-left: 20px;
    filter: brightness(.4);
}
.withoutFilter {
    filter: brightness(0);
}

.home__lists ul {
    padding: 0;
    margin-bottom: 0;
    margin-top: 25px;
}

.home__list p {
    font-size: 1.2em;
    margin-bottom: 1.3em;
}
.home__list p li {
    margin-left: 1em;
    list-style: none;
}

.home__lists_Flexx {
    display: flex;
}
.home__list_textos {
    list-style: none;
    margin-top: 25px;
    margin-bottom: 0;
    padding-top: 5px;
    margin-left: 18px;
    font-size: 1.2em;
}
.home__list_textos li {
    height: 54px;
    color: rgba(95, 95, 95, 1);
}
.home__list_textos a {
    text-decoration: none;
}
.home__list_textos a li {
    text-decoration: none;
    color: black;
}

/* buttontweet */
.home__contain_nextbutton input {
    font-size: 1em;
    font-weight: bold;
    color: white;
    border: none;
    width: 75%;
    border-radius: 50px;
    padding: 17px 0;
    background-color: rgba(29, 161, 242, 1);
    margin-bottom: 30px;
    margin-left: 10px;
    transition-duration: .5s;
}

.home__contain_nextbutton input:hover {
    background-color: rgb(32, 146, 218);
    transition-duration: .5s;
}

.home__last_margin{
    margin-bottom: 1em !important;
}

/* profile */
.home__profile_options {
    display: flex;
    margin-top: 1.1em;
    padding-left: 10px;
    margin-bottom: 20px;
}

.home__profile_options div img {
    margin-right: 15px;
    border-radius: 50px;
    width: 40px;
}

.home__profile_items {
    display: flex;
    flex-direction: column;
}

.home__profile_items .home__name {
    font-weight: 500;
}
.home__profile_items .home__username {
    color: rgb(83, 100, 113);
    margin-top: 2px;
}

.home__dots {
    padding-top: 8px;
}
.leftValueHideI {
    display: none;
}

@media ( max-width: 950px ) {
    .home__list_textos {
        display: none !important;
    }
    .login__main_home--svg {
        margin-left: 11px;
    }
    .hideItem {
        display: none;
    }
    .leftValueHideI {
        display: block;
    }
    .leftValueHide {
        display: none;
    }
}
@media ( max-height: 659px ) {
    .orderToDisappear1 {
        display: none;
    }
}
@media ( max-height: 602px ) {
    .orderToDisappear2 {
        display: none;
    }
}   
@media ( max-height: 551px ) {
    .orderToDisappear3 {
        display: none;
    }
}   
@media ( max-height: 494px ) {
    .orderToDisappear4 {
        display: none;
    }
}   
@media ( max-height: 446px ) {
    .orderToDisappear5 {
        display: none;
    }
}   